import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/about',
      name: 'about',
      component: resolve => {
      require(['../components/about.vue'], resolve)
      }
    },
    {
      path: '/develop',
      name: 'develop',
      component: resolve => {
      require(['../components/develop.vue'], resolve)
      }
    },
    {
      path: '/honor',
      name: 'honor',
      component: resolve => {
      require(['../components/honor.vue'], resolve)
      }
    },
    {
      path: '/hr',
          name: 'hr',
        component: resolve => {
      require(['../components/hr.vue'], resolve)
    }
    },
    {
      path: '/news',
          name: 'news',
        component: resolve => {
      require(['../components/news.vue'], resolve)
    }
    },
    {
      path: '/contact',
          name: 'contact',
        component: resolve => {
      require(['../components/contact.vue'], resolve)
    }
    },
    {
      path: '/product',
          name: 'product',
        component: resolve => {
      require(['../components/product.vue'], resolve)
    }
    },
    {
      path: '/food',
          name: 'food',
        component: resolve => {
      require(['../components/food.vue'], resolve)
    }
    },
    {
      path: '/orange',
          name: 'orange',
        component: resolve => {
      require(['../components/orange.vue'], resolve)
    }
    }
  ]
})



