<template>
  <div style="position: relative;width: 800px;margin: 0 auto;">
    <img src="../assets/logo.png" class="logo" @click="$router.push('/')">
    <div style="margin-left: 200px;">
      <div class="nav-item" v-for="(item,index) in items" @click="goto(item,index)" @mouseover="show(item)">{{item}}</div>
      <div class="sub" v-if="showAbout">
        <p @click="$router.push('/about')">公司简介</p>
        <p @click="$router.push('/develop')">发展历程</p>
        <p @click="$router.push('/honor')">公司荣誉</p>
      </div>
      <div class="subProduct" v-if="showProduct">
        <p @click="$router.push('/product')">新鲜果粒</p>
        <p @click="$router.push('/food')">美味冷菜</p>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'NavBar',
    methods: {
      goto (item,index) {
        this.$router.push(this.itemsrc[index])
      },
      show (item) {
        if (item === '关于我们') {
          this.showAbout = true
          this.showProduct = false
          setTimeout(() => {
            this.showAbout = false
          }, 2000)
          }else if (item === '产品展示') {
            this.showAbout = false
            this.showProduct = true
            setTimeout(() => {
                this.showProduct = false
            }, 2000)
          }else{
            this.showAbout = false
            this.showProduct = false
          }
      }
    },
    data () {
      return {
        showAbout: false,
        showProduct: false,
        items: ['关于我们', '新闻动态', '产品展示', '人才招聘', '联系我们'],
        itemsrc: ['/about', '/news', '/product', '/hr', '/contact'],
      }
    }
  }
</script>
<style>
  .logo {
    width: 180px;
    position: absolute;
    left: 0;
    top: 12px;
    cursor: pointer;
  }
  .p{
      cursor: pointer;
  }
  .subProduct {
    left: 435px;
    height: 90px !important;
    z-index: 999;
    position: absolute;
    top: 110px;
    width: 120px;
    background-color: rgba(255, 255, 255, .8);
    overflow: hidden;
    animation: enterPro .5s;
  }

  .nav-item {
    margin: 0;
    padding: 0;
    width: 120px;
    display: inline-block;
    padding-top: 70px;
    cursor: pointer;
    text-align: center;
    font-weight: 500;
    font-size: 17px;
  }

  .sub {
    z-index: 999;
    position: absolute;
    top: 110px;
    height: 120px;
    width: 120px;
    background-color: rgba(255, 255, 255, .8);
    overflow: hidden;
    animation: enter .5s;
  }

  @keyframes enterPro {
    from {
      height: 0;
    }
    to {
      height: 90px;
    }
  }

  @keyframes enter {
    from {
      height: 0;
    }
    to {
      height: 120px;
    }
  }
</style>



