<template>
  <div class="hello">
    <nav-bar></nav-bar>
    <div class="swiper-container" style="margin-top: 30px;height: 500px;">
      <div class="swiper-wrapper">
        <div class="swiper-slide">
          <img src="../assets/index.jpg" style="width:100%;">
        </div>
        <div class="swiper-slide">
          <img src="../assets/banner4.jpg" style="width:100%">
        </div>
        <div class="swiper-slide">
          <img src="../assets/banner5.jpg" style="width:100%">
        </div>
      </div>
    </div>
    <div style="text-align: center;margin-top: 100px;">
      <p>版权所有: 浙江可可佳食品有限公司 地址：浙江省衢州市航埠镇工业区</p>
      <p>Coptyright?2014 zjkekejia.com All Rights Reserved.</p>
    </div>
  </div>
</template>

<script>
import '../assets/swiper.css'
import Swiper from '../../static/swiper.min.js'
import NavBar from './nav.vue'
export default {
  name: 'hello',
  mounted () {
        this.banner = new Swiper('.swiper-container', {
          pagination: '.swiper-pagination',
          paginationClickable: true,
          autoplay: 3000
        })
      },
   data () {
     return {
       banner: null
     }
   },
  components: {
        NavBar
      }
}
</script>
<style>
  .hello{
    width:100vw;
    overflow:hidden;
  }
  .swiper-container {
    width: 100%;
    height: 100%;
  }

  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }
</style>



